:root {
  --transitionDuration: 150ms;
  --gradientLines : repeating-linear-gradient(
    -45deg,
    transparent,
    transparent 1px,
    rgba(100, 100, 100, 0.1) 1px,
    rgba(100, 100, 100, 0.1) 2px
  );
}

.layout-overlay {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  background: white;
}

.layout-overlay .layout-image {
  filter: contrast(0.3) saturate(0) brightness(1.5);
}

.layout-overlay:hover .layout-image {
  filter: none;
}

.layout-overlay .layout-color {
  width: 100%;
  height: 100%;
  position: absolute;
  background: linear-gradient(170deg, #ffffff 0%, #ffffff7a 100%);
  opacity: 1;
  transition: var(--transitionDuration);
  will-change: opacity;
}

.layout-overlay:hover .layout-color {
  background: linear-gradient(170deg, #ffffff 0%, transparent 100%);
}

.layout-overlay .layout-label {
  position: relative;
  line-height: 0.8;
  text-transform: uppercase;
  word-break: break-all;
  color: #d2d2d2;
  /* font-weight: bold; */
  left: 0;
  top: 0;
  margin-left: -2%;
  padding-left: 2%;
  letter-spacing: -1px;
}

.layout-overlay:hover .layout-label {
  color: black;
  transition: var(--transitionDuration);
  will-change: color;
}

@media screen and (max-width: 600px) {

  /* .layout-overlay .layout-image {
    filter: none;
  } */
  
  .layout-overlay .layout-label {
    color: #aeaeae;
  }

